    </b-modal>
<!-- Log detail popup -->
    <b-modal id="log-modal" centered header-class="p-0 custom-modal-header" body-class="p-0 modal-body2" footer-class="p-0 custom-modal-footer" dialog-class="custom-modal-dialog" content-class="custom-modal-content">
      <template #modal-header>
