        <div class="d-flex">
          <div class="d-flex detail-label2 justify-content-center align-items-center">
            <span>Detail</span>
          </div>
          <div class="d-flex detail-content2 align-items-center flex-fill">{{detail.data.detail}}</div>
        </div>
      </div>

      <div class="d-block modal-desc2" v-if="detail.type==='opr'">
        <div class="d-flex detail-top">
          <div class="d-flex detail-label justify-content-center align-items-center">
            <span>No.</span>
          </div>
          <div class="d-flex detail-content align-items-center flex-fill">{{detail.data.no}}</div>
        </div>
        <div class="d-flex">
          <div class="d-flex detail-label justify-content-center align-items-center">
            <span>Date</span>
          </div>
          <div class="d-flex detail-content align-items-center flex-fill">{{detail.data.date}}</div>
        </div>
        <div class="d-flex">
          <div class="d-flex detail-label justify-content-center align-items-center">
            <span>Device</span>
          </div>
          <div class="d-flex detail-content align-items-center flex-fill">{{detail.data.device}}</div>
        </div>
        <div class="d-flex">
          <div class="d-flex detail-label3 justify-content-center align-items-center">
            <span>Operation</span>
          </div>
          <div class="d-flex detail-content3 align-items-center flex-fill">{{detail.data.type}}</div>
        </div>
        <div class="d-flex">
          <div class="d-flex detail-label4 justify-content-center align-items-center">
            <span>Parameters</span>
          </div>
          <div class="d-flex detail-content4 align-items-center flex-fill">{{detail.data.desc}}</div>
        </div>
      </div>

      <template #modal-footer>
        <div class="d-flex align-items-stretch modal-footer">
          <button @click="closeDetail()" class="flex-fill modal-btn cancel">
            Close
          </button>
        </div>
      </template>
    </b-modal>
  </div>
</template>

<script>
import Navbar from '../components/Navbar.vue'

import { use } from "echarts/core";
import { CanvasRenderer } from "echarts/renderers";
import { LineChart } from "echarts/charts";
import { GridComponent, DataZoomComponent, LegendComponent, ToolboxComponent, TooltipComponent } from "echarts/components";
import VChart, { THEME_KEY } from "vue-echarts";
import axios from 'axios'

/* Echart  */
use([
  CanvasRenderer,
  LineChart,
  LegendComponent,
  DataZoomComponent,
  GridComponent,
  ToolboxComponent,
  TooltipComponent
]);

/*     */
const COLOR = [
  '#AED8A9',
  '#8D9BC9',
  '#6F619A',
  '#FF8DA0',
  '#868686',
  '#FB9A44',
  '#509F5D',
  '#82ACD3',
  '#FEC7BC',
]

const LEGEND = [ 'Tx0','Tx1','Tx2','Tx3', 'Rx0','Rx1','Rx2','Rx3', 'Temp.']

const MIN_LEFT = -100
const MAX_LEFT = 50
const MIN_RIGHT = -10
const MAX_RIGHT = 90
const INTERVAL_LEFT = 15
const INTERVAL_RIGHT = 10

/**
 *  type    csv   
 */
const getFileName = (type) => {
  var now = new Date()
  var year = now.getFullYear()
  var month = ('0' + (now.getMonth() + 1)).slice(-2)
  var day = ('0' + now.getDate()).slice(-2)
  var hours = ('0' + now.getHours()).slice(-2)
  var minutes = ('0' + now.getMinutes()).slice(-2)
  var seconds = ('0' + now.getSeconds()).slice(-2)
  
  return type + '-' + year + month + day + hours + minutes + seconds + '.csv';
}

/**
 *  Status   json > csv  
 */
const getStatusLogContent = (date, tx1, tx2, tx3, tx4, rx1, rx2, rx3, rx4, temp) => {
  var content = 'No.,Date,Tx0,Tx1,Tx2,Tx3,Rx0,Rx1,Rx2,Rx3,Temp.\n'

  for (var i = 0; i < date.length; i++) {
    content += `${i+1},${date[i]},${tx1[i]},${tx2[i]},${tx3[i]},${tx4[i]},${rx1[i]},${rx2[i]},${rx3[i]},${rx4[i]},${temp[i]}\n`
  }

  return content
}

const getLogContent = (raw, header) => {
  var content = header;

  for (var i = 0; i < raw.length; i++) {
    content += `${i + 1},${raw[i]}`
  }

  return content
}


/**
 * Vue  
 */
export default {
  name: 'log',
  data() {
    return {
      loadedStatusLog: true,
      loadedAlarmLog: true,
      loadedControlLog: true,
      loadedDetailLog: false,
      detailLogTimer: undefined,
      detailLogFile: '',
      errorMessage: '',
      option : {
        tooltip: {
          trigger: 'axis',
          position: function (pt) {
              return [pt[0], '10%'];
          }
        },
        toolbox: {
            feature: {
                dataZoom: {
                    yAxisIndex: 'none'
                },
            }
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '13%',
          containLabel: true
        },
        legend: {
          data: LEGEND
        },
        xAxis: {
            type: 'category',
            boundaryGap: false,
            axisLabel: {
              formatter: function (value) {
                return value;
              }
            },
            data: []
        },
        yAxis: [
          { // Left Y Axis
            type: 'value',
            name: '',
            min: MIN_LEFT,
            max: MAX_LEFT,
            interval: INTERVAL_LEFT,
            position: 'left',
            axisLine: {
                show: true,
                lineStyle: {
                    color: '#00000000'
                }
            },
          },
          { // Right Y Axis
            type: 'value',
            name: 'Temp(C)',
            position: 'right',
            min: MIN_RIGHT,
            max: MAX_RIGHT,
            interval: INTERVAL_RIGHT,
            axisLine: {
                show: true,
                lineStyle: {
                    color: '#00000000'
                }
            },
            axisLabel: {
                formatter: '{value} C'
            }
          }
        ],
        dataZoom: [{
            start: 90,
            end: 100
        }],
        backgroundColor: 'rgb(255, 255, 255)',
        series: [
            {
                name: LEGEND[0],
                type: 'line',
                itemStyle: {
                    color: COLOR[0]
                },
                data: []
            },
            {
                name: LEGEND[1],
                type: 'line',
                itemStyle: {
                    color: COLOR[1]
                },
                data: []
            },
            {
                name: LEGEND[2],
                type: 'line',
                itemStyle: {
                    color: COLOR[2]
                },
                data: []
            },
            {
                name: LEGEND[3],
                type: 'line',
                itemStyle: {
                    color: COLOR[3]
                },
                data: []
            },
            {
                name: LEGEND[4],
                type: 'line',
                itemStyle: {
                    color: COLOR[4]
                },
                data: []
            },
            {
                name: LEGEND[5],
                type: 'line',
                itemStyle: {
                    color: COLOR[5]
                },
                data: []
            },
            {
                name: LEGEND[6],
                type: 'line',
                itemStyle: {
                    color: COLOR[6]
                },
                data: []
            },
            {
                name: LEGEND[7],
                type: 'line',
                itemStyle: {
                    color: COLOR[7]
                },
                data: []
            },
            {
                name: LEGEND[8],
                type: 'line',
                itemStyle: {
                    color: COLOR[8]
                },
                yAxisIndex: 1,
                data: []
            },
        ]
      },
      alarmSrc: [],
      alarms: [],
      controlSrc: [],
      controls: [],
      detail: {
        title: '',
        type: 'alm',
        data: {}
      }
    }
  },
  created() {
    this.getStatusLog()
    this.getAlarmLog()
    this.getControlLog()
    this.initDetailLogState()
  },
  destroyed() {
  },
  computed: {
  },
  methods: {
    alarmLogBox() {
      if (this.$store.getters.isAdmin)
        return 'flex-fill d-block mr-24 card-box alarm-log-box-admin'
      return 'flex-fill d-block mr-24 card-box alarm-log-box'
    },
    getAlarmLog: function() {
      this.loadedAlarmLog = true
      axios({
        method: 'get',
        url: this.$store.getters.apiServer + '/php/log.php',
        params: {
          key: this.$store.getters.sessionKey,
          id: this.$store.getters.userid,
          type: 'alm'
        },
      }).then ((resp) => {
        if (resp.status == 200 && resp.data.state == 'Success') {
          this.alarmSrc = resp.data.data
          const len = (this.alarmSrc.length > 300) ? 300 : this.alarmSrc.length
          for (var idx = 0; idx < len; idx++) {
            const tmp = this.alarmSrc[idx].split(',')
            const obj = {
              no: idx+1,
              date: tmp[0],
              id: tmp[1],
              type: tmp[2],
              desc: tmp[3],
              detail: tmp[4]
            }
            this.alarms.push(obj)
          }
          this.loadedAlarmLog = false
        }
      }).catch(err => {
        this.loadedAlarmLog = false
        this.errorMessage = err.message
        this.$bvModal.show('error-modal')
      })
    },
    getControlLog: function() {
      this.loadedControlLog = true
      axios({
        method: 'get',
        url: this.$store.getters.apiServer + '/php/log.php',
        params: {
          key: this.$store.getters.sessionKey,
          id: this.$store.getters.userid,
          type: 'opr'
        },
      }).then ((resp) => {
        if (resp.status == 200 && resp.data.state == 'Success') {
          this.controlSrc = resp.data.data
          const len = (this.controlSrc.length > 300) ? 300 : this.controlSrc.length
          
          for (var idx = 0; idx < len; idx++) {
            const tmp = this.controlSrc[idx].split(',')
            const obj = {
              no: idx+1,
              date: tmp[0],
              device: tmp[1],
              type: tmp[2],
              desc: tmp[3]
            }
            this.controls.push(obj)
          }
          this.loadedControlLog = false
        }
      }).catch(err => {
        this.loadedControlLog = false
        this.errorMessage = err.message
        this.$bvModal.show('error-modal')
      })
    },
    getStatusLog: function() {
      this.loadedStatusLog = true
      axios({
        method: 'get',
        url: this.$store.getters.apiServer + '/php/status-log.php',
        params: {
          key: this.$store.getters.sessionKey,
          id: this.$store.getters.userid
        },
      }).then ((resp) => {
        if (resp.status == 200 && resp.data.state == 'Success') {
          const c = resp.data.data
          
          this.option.xAxis.data = c.date
          this.option.series[0].data = c.tx1
          this.option.series[1].data = c.tx2
          this.option.series[2].data = c.tx3
          this.option.series[3].data = c.tx4
          this.option.series[4].data = c.rx1
          this.option.series[5].data = c.rx2
          this.option.series[6].data = c.rx3
          this.option.series[7].data = c.rx4
          this.option.series[8].data = c.temp

          this.loadedStatusLog = false
        }
      }).catch(err => {
        this.loadedStatusLog = false
        this.errorMessage = err.message
        this.$bvModal.show('error-modal')
      })
    },
    deleteStatusLog: function() {
      this.loadedStatusLog = true
      axios({
        method: 'delete',
        url: this.$store.getters.apiServer + '/php/status-log.php',
        params: {
          key: this.$store.getters.sessionKey,
          id: this.$store.getters.userid
        },
      }).then ((resp) => {
        if (resp.status == 200 && resp.data.state == 'Success') {
          this.option.xAxis.data = []
          this.option.series[0].data = []
          this.option.series[1].data = []
          this.option.series[2].data = []
          this.option.series[3].data = []
          this.option.series[4].data = []
          this.option.series[5].data = []
          this.option.series[6].data = []
          this.option.series[7].data = []
          this.option.series[7].data = []

          this.loadedStatusLog = false
        }
      }).catch(err => {
        this.loadedStatusLog = false
        this.errorMessage = err.message
        this.$bvModal.show('error-modal')
      })
    },
    saveStatusLog: function() {
      if (this.option.xAxis.data.length <= 0) {
        this.errorMessage = 'No data'
        this.$bvModal.show('error-modal')
      } else {
        var fileName = getFileName('status-log')
        var content = getStatusLogContent(
          this.option.xAxis.data,
          this.option.series[0].data,
          this.option.series[1].data,
          this.option.series[2].data,
          this.option.series[3].data,
          this.option.series[4].data,
          this.option.series[5].data,
          this.option.series[6].data,
          this.option.series[7].data,
          this.option.series[7].data
        );
        var blob = new Blob([content], { type: 'text/csv' });
        var objURL = window.URL.createObjectURL(blob);
      
        if (window.__Xr_objURL_forCreatingFile__) {
          window.URL.revokeObjectURL(window.__Xr_objURL_forCreatingFile__);
        }
        window.__Xr_objURL_forCreatingFile__ = objURL;
        var a = document.createElement('a');
        a.download = fileName;
        a.href = objURL;
        a.click();
      }
    },
    saveDetailLog: function() {
      this.loadedDetailLog = true
      axios({
        method: 'put',
        url: this.$store.getters.apiServer + '/php/detail-log.php',
        params: {
          key: this.$store.getters.sessionKey,
          id: this.$store.getters.userid
        },
      }).then ((resp) => {
        if (resp.status == 200 && resp.data.state == 'Success') {
          this.detailLogTimer = setInterval(this.getDetailLogState, 1000)
        } else {
          this.loadedDetailLog = false
        }
      }).catch(err => {
        this.loadedDetailLog = false
        this.errorMessage = err.message
        this.$bvModal.show('error-modal')
      })
    },
    initDetailLogState: function () {
      axios({
        method: 'get',
        url: this.$store.getters.apiServer + '/php/detail-log.php',
        params: {
          key: this.$store.getters.sessionKey,
          id: this.$store.getters.userid
        },
      }).then ((resp) => {
        if (resp.status == 200 && resp.data.state == 'Success') {
          this.detailLogFile = resp.data.data.detaillog_file
        }
      }).catch(err => {
        this.errorMessage = err.message
        this.$bvModal.show('error-modal')
      })
    },
    getDetailLogState: function () {
      axios({
        method: 'get',
        url: this.$store.getters.apiServer + '/php/detail-log.php',
        params: {
          key: this.$store.getters.sessionKey,
          id: this.$store.getters.userid
        },
      }).then ((resp) => {
        if (resp.status == 200 && resp.data.state == 'Success') {
          if (resp.data.data.detaillog === 22) {
            clearInterval(this.detailLogTimer)
            this.detailLogFile = resp.data.data.detaillog_file
            this.downloadDetailLog(resp.data.data.detaillog_file)
          }
        }
      }).catch(err => {
        this.loadedDetailLog = false
        this.errorMessage = err.message
        this.$bvModal.show('error-modal')
      })
    },
    downloadDetailLog: function (filename) {
      axios({
        method: 'get',
        url: this.$store.getters.apiServer + '/php/download-detail-log.php',
        responseType: 'arraybuffer',
        params: {
          key: this.$store.getters.sessionKey,
          id: this.$store.getters.userid,
          file: filename
        },
      }).then ((resp) => {
        if (resp.status == 200) {
          this.loadedDetailLog = false
          let blob = new Blob([resp.data], {
            type: resp.headers['content-type'],
          });

          var objURL = window.URL.createObjectURL(blob);
          if (window.__Xr_objURL_forCreatingFile__) {
            window.URL.revokeObjectURL(window.__Xr_objURL_forCreatingFile__);
          }
          window.__Xr_objURL_forCreatingFile__ = objURL;
          var a = document.createElement('a');
          a.download = filename;
          a.href = objURL;
          a.click();
          window.URL.revokeObjectURL(a);
        }
      }).catch(err => {
        this.loadedDetailLog = false
        this.errorMessage = err.message
        this.$bvModal.show('error-modal')
      })
    },
    closeErrorPopup: function() {
      this.$bvModal.hide('error-modal')
    },
    formatter: function(src, max, sym) {
      if (!src) return ''
      if (src.length <= max) return src

      return `${src.substring(0, max-1)}${sym}`
    },
    showAlarmDetail: function(alarm) {
      this.detail.title = "Alarm Log"
      this.detail.type = 'alm'
      this.detail.data = alarm
      this.$bvModal.show('log-modal')
    },
    showCtrlDetail: function(ctrl) {
      this.detail.title = "Control Log"
      this.detail.type = 'opr'
      this.detail.data = ctrl
      this.$bvModal.show('log-modal')
    },
    closeDetail: function() {
      this.$bvModal.hide('log-modal')
    },
    saveLog: function(type) {
      var fileName = getFileName('alarm-log')
      var content = getLogContent(this.alarmSrc, 'No.,Date,ID,Type,Description,Detail\n')
      if (type === 'opr') {
        fileName = getFileName('ctrl-log')
        content = getLogContent(this.controlSrc, `No.,Date,Device,Operation,Parameters\n`)
      }
      
      var blob = new Blob([content], { type: 'text/csv' });
      var objURL = window.URL.createObjectURL(blob);
    
      if (window.__Xr_objURL_forCreatingFile__) {
        window.URL.revokeObjectURL(window.__Xr_objURL_forCreatingFile__);
      }
      window.__Xr_objURL_forCreatingFile__ = objURL;
      var a = document.createElement('a');
      a.download = fileName;
      a.href = objURL;
      a.click();
    },
  },
  components: {
    'v-navbar': Navbar,
    'v-echart': VChart
  },
  provide: {
    [THEME_KEY]: "dark"
  },
}
</script>
